.home-page{
    .header{
        overflow: hidden;
        padding: 15px;
        position: relative;
        .right-img{
            padding: 15px;
            img{
                width: 100%;
            }
            .header-info{
                position: absolute;
                color: #222222;
                left: 8%;
                top:30%;
                h2{
                    font-family: 'Dancing Script', cursive;
                    font-size: 50px;
                    font-weight: 700;
                    margin: 0;
                    position: relative;
                    animation: header 1s;
                    animation-fill-mode: forwards;
                    opacity: 0;
                    animation-delay: 1s;
                }
                p{
                    font-size: 20px;
                    position: relative;
                    animation: header 1.2s;
                    animation-fill-mode: forwards;
                    opacity: 0;
                    animation-delay: 1.5s;
                }
                a{
                    text-decoration: none;
                    background: #f2cd85;
                    color: #FFF;
                    padding: 8px 13px;
                    font-size: 19px;
                    position: relative;
                    animation: header 1.5s;
                    animation-fill-mode: forwards;
                    opacity: 0;
                    animation-delay: 1.4s;
                }
            }
        }
        .left-img{
            padding: 15px;
            img{
                height: 520px;
                width: 100%;
            }
        }
    }
    .services{
        margin:0 30px 30px;
        border: 1px solid #e2e2e2;
        border-radius: 5px;
        padding: 30px;
        overflow: hidden;
        .services-item{
            text-align: center;
            h3{
                font-size: 16px;
                font-weight: 700;
                text-transform: capitalize;
                margin:0;
            }
            p{
                color:#8b8b8b;
                font-weight: 600;
                text-transform: capitalize;
                margin:0;
                margin-top:8px;
            }
        }
        .services-item:not(:first-child){
            border-left: 2px solid #e3dede;
        }
    }
    .new-products{
        padding:30px 15px;
        overflow: hidden;
        .new-products-item{
            padding:0 15px;
            img{
                width: 100%;
            }
        }
    }
    .top-categories{
        padding:30px;
        .section-name{
            border-bottom: 1px solid #e2e2e2;
            margin-bottom: 30px;
            .top-categories-title{
                font-size: 22px;
                border-bottom:3px solid #f2cd85;
                width: 200px;
                padding-bottom: 10px;
                margin-bottom: 0;
            }
        }
        .title-slbody .buttons{
            position: relative;
            top: -475px;
        }
        .title-slbody .buttons .prev ,.title-slbody .buttons .next{
            border: 1px solid #e2e2e2;
            transition: all 0.2s ease-in-out 0s;
            top: 10px;
            font-size: 17px;
            padding: 0px 6px;
        }
        .title-slbody .buttons .prev{   
            right: 40px;
        }
        .title-slbody .buttons .next{
            right: 10px;
        }
    }
    .new-products-two{
        padding:15px;
        overflow: hidden;
        .new-products-two-item{
            padding: 15px;
            position: relative;
            img{
                width: 100%;
            }
            .item-info{
                position:absolute;
                top:50px;
                right:70px;
                h2{
                    font-weight: normal;
                    font-size: 30px;
                    span{
                        margin-bottom: 2px;
                        font-weight: bold;
                        display: block;
                    }
                }
                a{
                    text-decoration: none;
                    background: #f2cd85;
                    color: #FFF;
                    padding: 8px 27px;
                    font-size: 19px;
                    position: relative;
                    transition:0.5s;
                    &:hover{
                        background: #000;
                    }
                }
            }
            .item-two{
                top:50px;
                left:70px;
            }
        }
    }
    .our-products{
        padding:30px;
        position: relative;
        .collapse-conatiner{
            width:100%;
            position: relative;
        }
        .collapse-conatiner input[type="radio"]{
            display:none
        }
        .collapse-conatiner .collapse-control{
            right: 116px;
            position: absolute;
            top: -80px;
        }
        .collapse-conatiner .collapse-control .table-title {
            float: left;
            text-align:center;
            margin: 15px 0 0;
            padding: 0 15px;
            font-family: "Segoe UI Historic", "Segoe UI", Helvetica, Arial, sans-serif;
            transition:0.5s;
            cursor: pointer;
            font-weight: bold;
            font-size: 18px;
        }
        .collapse-conatiner .collapse-control .table-title:not(:first-child){
            border-left:2px solid #cccccc;
        }
        .collapse-conatiner .table-data{
            clear: both;
            display: none;
            animation: fadeIn 1s;
        }
        @keyframes fadeIn {
            from {
                opacity:0;
            }
            to {
                opacity:1;
            }
        }
        /*hover for table title */
        .collapse-conatiner #input-one:checked ~ .collapse-control .title-one,
        .collapse-conatiner #input-two:checked ~ .collapse-control .title-two,
        .collapse-conatiner #input-three:checked ~ .collapse-control .title-three {
            color:#f2cd85;
            cursor: pointer;
        }
        
        /*click for table data */
        .collapse-conatiner #input-one:checked ~ .collapse-data .table-one{
            display: block;
        }
        .collapse-conatiner #input-two:checked ~ .collapse-data .table-two{
            display: block;
        }
        .collapse-conatiner #input-three:checked ~ .collapse-data .table-three{
            display: block;
        }
        @media (max-width:557px) {
            .collapse-conatiner{
                width: 100%;
            }
            .collapse-conatiner .collapse-control{
                width:100%;
            }
        }
        .section-name{
            border-bottom: 1px solid #e2e2e2;
            margin-bottom: 30px;
            .top-categories-title{
                font-size: 22px;
                border-bottom:3px solid #f2cd85;
                width: 200px;
                padding-bottom: 10px;
                margin-bottom: 0;
            }
        }
        .product-slbody .buttons{
            top: -435px;
        }
        .product-slbody .buttons .prev ,.product-slbody .buttons .next{
            border: 1px solid #e2e2e2;
            transition: all 0.2s ease-in-out 0s;
            top: 10px;
            font-size: 17px;
            padding: 0px 6px;
        }
        .product-slbody .buttons .prev{   
            right: 40px;
        }
        .product-slbody .buttons .next{
            right: 10px;
            &:hover{
                background:#000;
                color:#FFF;
            }
        }
    }
    .classic{
        padding:15px;
        overflow: hidden;
        .classic-item{
            margin: 0 0 15px 0;
            height: 600px;
            padding: 15px 15px 0 15px;
            overflow: hidden;
            img{
                width: 100%;
                height:100%;
            }
            .img-top{
                height:calc(100% / 2 - 14px);
                margin-bottom: 15px;
            }
            .img-bottom{
                height:calc(100% / 2 - 14px);
                margin-top: 15px;
            }
        }
        .classic-item:last-child,.classic-item:first-child{
            .img-hover{height:100%}
        }
    }
    .our-brands{
        padding: 30px;
        overflow: hidden;
        position: relative;
        .section-name{
            border-bottom: 1px solid #e2e2e2;
            margin-bottom: 30px;
            .top-categories-title{
                font-size: 22px;
                border-bottom:3px solid #f2cd85;
                width: 200px;
                padding-bottom: 10px;
                margin-bottom: 0;
            }
        }
        .logo-slbody .buttons{
            position: absolute;
            top: 41px;
            right: 53px;
        }
        .logo-slbody .buttons .prev ,.logo-slbody .buttons .next{
            border: 1px solid #e2e2e2;
            transition: all 0.2s ease-in-out 0s;
            top: 10px;
            font-size: 17px;
            padding: 0px 6px;
            position: relative;
            &:hover{
                background:#000;
                color:#FFF;
            }
        }
        .logo-slbody .buttons .prev{   
            left: -6px;
            top: 14px;
        }
        .logo-slbody .buttons .next{
            right: -23px;
            top: -12px;
        }
    }
    
}
