/* hover for slider button */
.buttons .next{
    transition:0.6s;
    &:hover{
        background:#000;
        color:#FFF;
    }
}
.buttons .prev{
    transition:0.6s;
    &:hover{
        background:#000;
        color:#FFF;
    }
}
/* start header info inimation */
@keyframes header{
    0%{opacity: 0;bottom:-50px;}
    100%{opacity: 1;bottom:0;}
}