@import '../varibales/varibal.scss';
/* start upper nav */
.uppernav {
    background:#000;
    overflow: hidden;
    color:#FFF;
    font-family:Montserrat, sans-serif;
    font-size:14px;
    font-weight:600;
    padding:7px 0;
    ul{
        margin: 0;
    }
    .welcome{
        float:$floatLeft;
    }
    .top-menu{
        float:$floatRight;
        font-size: 14px;
        font-weight: normal;
        color: #FFF;
        #menu-toggler-lan{
            display:none;
        }
        #menu-toggler-lan:checked ~ .lan-menu-data{
            display:block;
        }
        .heading-top-menu{
            margin-right:25px;
            cursor: pointer;
        }
        .lan-menu-data{
            display: none;
            background-color:#FFF;
            position: absolute;
            top:33px;
            right: 16px;
            padding: 0;
            li{
                padding:2px 9px;
                a{
                    text-decoration:none;
                    color: #222222;
                }
            }
        }
    }
}
/* end upper nav */
/* start prand */
.prand{
    padding:10px 35px;
    overflow: hidden;
    .prand-search {
        text-align: center;
        line-height: 4;
        input{
            width:100%;
            border: 1px solid #e2e2e2;
            border-radius: 5px;
            max-width: 80%;
            padding: 11px 10px;
            color: #333333;
            font-weight: 300;
            font-size: 14px;
        }
        button{
            margin-left: -95px;
            padding: 9px 20px;
            border-radius: 0 5px 5px 0;
            font-size: 14px;
            font-weight: 600;
            background-color: #f2cd85;
            border: 2px solid #f2cd85;
            color:#FFF;
        }
    }
}
/* end prand */
/* start navbar */
nav{
    background-color:#000;
    overflow: hidden;
    .nav-category{
        cursor: pointer;
        position: absolute;
        color:#000;
        z-index: 5;
        label{
            background-color:$mainColor;
            padding: 6px 15px;
            font-size: 22px;
            margin-top: 0px;
            display: inline-block;
            cursor: pointer;
        }
        input#menu-toggler {
            display: none;
        }
        input:checked ~ .category-menu-data {
            display:block;
        }
        .category-menu-data{
            display: none;
            .nav-links-menu{
                background:#FFF;
                padding:10px;
                display: none;
                p{
                    margin: 0;
                    margin-bottom: 20px;
                }
                p:after{
                    content:'';
                    height: 2px;
                    width: 45px;
                    position: absolute;
                    top: 37px;
                    left: 10px;
                    background:#f2cd85;
                }
                ul{
                    margin:0;
                    padding:0;
                    li{
                        border-bottom: 1px solid #e2e2e2;
                        a{
                            text-decoration:none;
                            color: #666666;
                            font-size: 14px;
                            cursor: pointer;
                            display: inline-block;
                            text-align: center;
                            padding: 10px;
                        }
                    }
                }
            }
            .category-links{
                margin:0;
                padding: 10px;
                background: #FFF;
                width:100%;
                position: relative;
                P{
                    margin: 0;
                    margin-bottom: 20px;
                }
                p:after{
                    content:'';
                    height: 2px;
                    width: 80px;
                    width: 100px;
                    position: absolute;
                    top: 40px;
                    left: 10px;
                    background:#f2cd85;
                }
                .category-item{
                    margin: 0;
                    overflow: visible;
                    padding: 10px;
                    a{
                        text-decoration:none;
                        display: block;
                        color: #666666;
                        font-size: 14px;
                        font{
                            position: absolute;
                            right: 22px;
                            font-size: 11px;
                        }
                    }
                }
                .category-item:not(:last-child){
                    border-bottom: 1px solid #e2e2e2;
                }
                /* start sub category links => sub clothes linkes */
                .sub-category-links{
                    left: 0;
                    border: 1px solid #EEE;
                    margin-left: 100%;
                    background: #FFF;
                    padding: 5px 15px;
                    position: absolute;
                    top: 0;
                    width: 220px;
                    display:none;
                    li{
                        a{
                            text-decoration:none;
                            color: #666666;
                            font-size: 12px;
                            cursor: pointer;
                            display: inline-block;
                            text-align: center;
                        }
                    }
                    li:not(:last-child){
                        padding-bottom:5px;
                    }
                }
                .sub-category-item:hover .sub-category-links{
                
                    display:inline-block;
                }
            }
        }
    }
    .nav-links{
        text-align: center;
        ul{
            margin:0;
            li{
                text-transform: uppercase;
                float: left;
                margin-left:15px;
                font-size: 17px;
                font-weight: 600;
                padding:10px;
                a{
                    text-decoration:none;
                    display: block;
                    color:#FFF;
                    font-size: 14px;
                }
            }
        }
        
    }
}
/* start  navbar media */
@media(max-width:767px){
    /* start uppernav */
    .uppernav {
        ul {
        margin:0;
        padding-left:10px;
        }
        .uppernav .top-menu .heading-top-menu{
            margin-right: 20px;
        }
    }
    /* start prand name */
    .prand{
        padding:0;
        padding-top: 10px;
        .prand-name{
            text-align: center;
            height: 50px;
            padding-bottom: 10px;
            img{
                height:100%;
            }
        }
        .prand-search{
            background-color: #000;
            input{
                max-width: 90%;
            }
            button{
                padding: 10px 20px
            }
        }
    }
    /* start navbar */
    nav {
        .nav-category {
            label{
                position: absolute;
                top: -119px;
                font-size: 32px;
                left: 0px;
                background-color:transparent;
            }
            .category-menu-data {
                .nav-links-menu{
                    display:block;
                }
                .category-links{
                    z-index:5;
                    .sub-category-links{
                        border:none;
                        margin-left:0; 
                        padding: 5px 15px;
                        position: relative;
                        width:100%;
                    
                    }
                }
            }
        }
        .nav-links{
            display: none;
        }
    }
}
@media(min-width:768px){
    .prand{
        .prand-search{
            width:75%;
        }
    }
}
@media(min-width:991px){
    .prand{
        .prand-search{
            width:50%;
        }
    }
}