@import '../varibales/varibal.scss';
footer{
    margin-top:30px;
    overflow: hidden;
    .news-letter{
        background-color:#222;
        overflow: hidden;
        padding: 50px;
        position: relative;
        h2{
            display: inline-block;
            margin: 0;
            color:#FFF;
            font-size: 24px;
        }
        p{
            display: inline-block;
            color:#FFF;
            margin-left: 20px;
            padding-left: 20px;
            border-left:2px solid #FFF;
            font-size: 18px;
            text-transform: capitalize;
        }
    }
    .send-message{ 
        input[type=email]{
        border: none;
        padding: 14px 15px;
        width: 100%;
        margin-top: 11px;
        border-radius: 5px 0 0 5px;
        font-size: 16px;
        color: #495057;
        }
        input[type=submit]{
            border: 2px solid #f2cd85;
            background-color: #f2cd85;
            padding: 13px 30px;
            position: absolute;
            right: 46px;
            top: 61px;
            border-radius: 0 5px 5px 0;
            font-size: 14px;
        }
    }
    .main-footer{
        padding:60px 15px 0;
        overflow: hidden;
        .company-info{
            padding: 0 15px;
            p{
                margin-top: 15px;
                text-transform: capitalize;
                font-size: 16px;
                color:#222;
                line-height: 25px;
                margin-bottom: 0px;
            }
            ul{
                margin: 0;
                padding: 0;
                margin-top:10px;
                li{
                    display: inline-block;
                    margin-right: 15px;
                    a{
                        text-decoration:none;
                        color:#222;
                    }
                }
            }
        }
        .information{
            padding:0 15px;
            h3{
                text-transform: uppercase;
                font-size: 18px;
                font-weight: 600;
                margin-top:0;
            }
            ul{
                margin:0;
                padding: 0;
                li{
                    margin-top: 9px;
                    font-size: 16px;
                    transition:0.5s;
                    margin-left: -9px;
                    font{
                        opacity: 0;
                    }
                    a{
                        text-decoration:none;
                        color:#222;
                        transition:0.5s;
                        &:hover{
                            margin-left:10px;
                            color: #f2cd85;
                        }
                        &:hover font{
                            opacity: 1;
                        }
                    }
                }
            }
            .info-menu{
                display:none;
            }
        }
        .contact{
            ul{
                li{
                    margin-left:7px;
                    font-weight: bold;
                }
            }
        }
    }
    .copy-right{
        margin:60px 30px 10px;
        overflow: hidden;
        text-align: center;
        border-top: 1px solid #e1ded8;
        padding-top: 20px;
    }
}
/* start media query */
@media(max-width:990px){
    footer{
        .news-letter{
            padding:15px;
            h2{
                font-size: 18px;
                margin-top:10px;
            }
            p{
                display:block; 
                color: #FFF;
                margin-left:0;
                padding-left:0;
                border-left:none;
                font-size: 15px;
            }
            .send-message{
                input[type=email]{
                    border-radius:5px;
                    padding: 12px 12px;
                    font-size: 14px;
                }
                input[type=submit]{
                    position: relative;
                    top:0;
                    right:0;
                    margin-top:15px;
                    width: 100%;
                }
            } 
        }
        .main-footer {
            padding: 15px;
            .company-info{
                padding:0;
                text-align: center;
                margin-bottom: 15px;
                width: 100%;
                P{
                    font-size: 14px;
                }
            }
            .information{
                position: relative;
                padding:0;
                width: 100%;
                .info-full-screen{
                    display: none;
                }
                .info-menu{
                    display: block;
                    font-size: 20px;
                    #menu-toggler-info{
                        display:none;
                    }
                    #menu-toggler-info:checked ~ .menu-info-data{
                        display:block;
                    }
                    .menu-heading{
                        cursor: pointer;
                        width: 100%;
                        display: inline-block;
                        padding: 10px 0;
                        text-transform: uppercase;
                        font-size: 15px;
                        font{
                            position: absolute;
                            right: 10px;
                        }
                    }
                    .menu-info-data,.menu-company-data{
                        z-index: 5;
                        display: none;
                        padding: 0;
                        margin-bottom: 15px;
                        li{
                            padding:2px 9px;
                            a{
                                text-decoration:none;
                                color: #222222;
                            }
                        }
                    }
                }
            }
        }
        .company{
            #menu-toggler-company{
                display:none;
            }
            #menu-toggler-company:checked ~ .menu-company-data{
                display:block;
            }
        }
        .contact{
            #menu-toggler-contact{
                display:none;
            }
            #menu-toggler-contact:checked ~ .menu-contact-data{
                display:block;
            }
            .menu-contact-data{
                display: none;
            }
        }
        .copy-right{
            margin: 0 30px 10px;
        }
    }
}